export default {
  GETEvents: {
    url: "/events",
    type: "get",
  },
  GETDiscordLink: {
    url: "/login/discord",
    type: "get",
    queryKeys: ["redirect_url"],
  },
  POSTCheckToken: {
    url: "/token/discord",
    type: "post",
    jsonKeys: ["discordCode"],
  },
  POSTLogIn: {
    url: "/login",
    type: "post",
    jsonKeys: ["username"],
  },
  GETCharacterList: {
    url: "/charactersList",
    type: "get",
  },
  POSTCreateCharacter: {
    url: "/createCharacter",
    type: "post",
    jsonKeys: ["name", "password", "display_name"],
  },
};
