import uniqueString from "unique-string";

export function dumbMutation(state, { field, value }) {
  state[field] = value;
}

export function setUserAuthData(state, data) {
  state.userAuthData = data;
}
export function pushToTray(state, text) {
  state.trayItems.push({
    text,
    id: uniqueString(),
  });
}
export function removeFromTray(state, id) {
  let idToRemove = state.trayItems.findIndex((item) => item.id === id);
  state.trayItems.splice(idToRemove, 1);
}
